import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      component: () =>
        import ('../components/home')
    },
    // 
    {
      path: '/project',
      component: () =>
        import ('../components/project')
    },
    // 
    {
      path: '/submitProject',
      component: () =>
        import ('../components/submit-project')
    },
    // 
    {
      path: '/submitClaim',
      component: () =>
        import ('../components/submit-claim'),
      props: (route) => ({
        id: route.query.id
      })
    },
    // 
    {
      path: '/projectDetail',
      component: () =>
        import ('../components/project-detail'),
      props: (route) => ({
        id: route.query.id,
        tab: route.query.tab,
      })
    },
    // 
    {
      path: '/myProject',
      component: () =>
        import ('../components/my-project')
    },
    // 
    {
      path: '/projectJoined',
      component: () =>
        import ('../components/project-joined')
    },
    // 
    {
      path: '/projectApplied',
      component: () =>
        import ('../components/project-applied')
    },
    // 
    {
      path: '/discussion',
      component: () =>
        import ('../components/discussion'),
      props: (route) => ({
        id: route.query.id
      })
    },
  ],
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})


