import Vue from 'vue'
import Vuex from 'vuex'
import i18n from 'static/js/i18n'

Vue.use(Vuex)

const state = { //state
  account: null,
  lang: i18n.locale,
  isLoading: true,
  domain: process.env.NODE_ENV == 'development' ? 'http://rpcdev.xinchain.org/' : 'https://rpc.medishares.net/',
}
const getters = { //state()
  account(state) { //
    return state.account
  },
  isLoading(state) {
    return state.isLoading
  },
  getLang(state) {
    return state.lang
  },
  getDomain(state) {
    return state.domain
  },
}
const mutations = {
  // 
  setAccount(state, val) {
    state.account = val;
  },
  setLoading(state, val) {
    state.isLoading = val;
  },
  setLang(state, val) {
    i18n.setUserLanguage(val)
    state.lang = val;
  },
  logout(state) {
    state.account = null;
    window.location.reload();
  },
}
const actions = {
  setAccount(context, val) {
    context.commit('setAccount', val)
  },
  setLoading(context, val) {
    context.commit('setLoading', val)
  },
  setLang(context, val) {
    context.commit('setLang', val)
  },
  logout(context) {
    context.commit('logout')
  },
}
export default new Vuex.Store({
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions
})


